<template>
  <div class="search">
    <div class="search-input">
      <input type="text" class="input-text" placeholder="请输入搜索内容" v-model="value" />
      <input type="button" class="input-btn" value="搜索" @click="getKeywordData()" />

      <input type="button" class="input-btn" value="关闭" @click="backPage()" />
    </div>
    <div class="search-list">
      <ul>
        <li
          v-for="(item, index) in KeywordData"
          :key="index"
          @click="goDetails(item.id)"
        >{{item.title}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import "@/assets/css/search.less";
export default {
  data() {
    return {
      value: "",
      KeywordData: []
    };
  },

  created() {
    // if (!this.$store.isLogin) {
    //   this.$router.push({ name: "User" });
    // }
  },

  methods: {
    // 返回页面
    backPage() {
      this.$router.back(-1);
    },

    // 获得关键词数据
    getKeywordData() {
      this.axios({
        // url: "https://berkelium-1.github.io/jsons/keyword.json",

        url: "/keyword",
        params: {
          apikey: this.apikey,
          keyword: this.value,
          pageType: "information"
        }
      })
        .then(res => {
          
          this.KeywordData = res.data.data;
          //
        })
        .catch(err => {});
    },
    //item_id
    goDetails(itemId) {
      this.$store.commit("getItemId", itemId);
      this.$router.push({ name: "Details" });
    }
  }
};
</script>
